@import "../../scss/variables";
@import "../../scss/mixins";

.link-style {
  a {
    padding: 0.4rem;
    @include transition(1s);

    &:hover {
      color: $secondary-color;
    }
  }
}

footer.horizontal {
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 1rem;
  @extend .link-style;
}

footer.vertical {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 2rem;
  .social-icons {
    display: flex;
    flex-direction: column;
    position: fixed;
    bottom: 1rem;
    @extend .link-style;
  }
  .copyright {
    position: fixed;
    bottom: 1rem;
    right: 3rem;
    writing-mode: vertical-lr;
    color: white;
  }
}

footer.middle {
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  gap: 1rem;
  @extend .link-style;
}

i.fab.fa-twitter {
  color: white;
}

i.fab.fa-facebook {
  color: white;
}

i.fab.fa-instagram {
  color: white;
}

i.fab.fa-github {
  color: white;
}
